import { create } from 'zustand'
import { productsData } from '../constants/productsData'

// API –±–∞–∑–æ–≤–∏–π URL
const API_BASE_URL = process.env.NODE_ENV === 'production'
  ? '/api'
  : 'http://localhost:3001/api'

// API –∫–ª—ñ—î–Ω—Ç –¥–ª—è –ø—É–±–ª—ñ—á–Ω–æ–≥–æ —Å–∞–π—Ç—É
const apiClient = {
  async get(endpoint) {
    const response = await fetch(`${API_BASE_URL}${endpoint}`)
    if (!response.ok) throw new Error(`HTTP ${response.status}`)
    return response.json()
  }
}

const useProductsStore = create((set, get) => ({
  // –°—Ç–∞–Ω –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
  products: [],
  isLoading: false,
  error: null,
  isInitialized: false,

  // –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑ API
  loadProducts: async (forceRefresh = false) => {
    const { isInitialized } = get()

    // –Ø–∫—â–æ –¥–∞–Ω—ñ –≤–∂–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω—ñ —ñ –Ω–µ –ø–æ—Ç—Ä—ñ–±–Ω–æ –ø—Ä–∏–º—É—Å–æ–≤–µ –æ–Ω–æ–≤–ª–µ–Ω–Ω—è
    if (!forceRefresh && isInitialized) {
      console.log('üîÑ –ü—Ä–æ–¥—É–∫—Ç–∏ –≤–∂–µ –∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω—ñ –¥–ª—è –ø—É–±–ª—ñ—á–Ω–æ–≥–æ —Å–∞–π—Ç—É')
      return { success: true, data: get().products }
    }

    set({ isLoading: true, error: null })

    try {
      console.log('üîÑ –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑ API –¥–ª—è –ø—É–±–ª—ñ—á–Ω–æ–≥–æ —Å–∞–π—Ç—É:', `${API_BASE_URL}/products`)
      const response = await apiClient.get('/products')
      console.log('‚úÖ –û—Ç—Ä–∏–º–∞–Ω–æ –ø—Ä–æ–¥—É–∫—Ç–∏ –¥–ª—è –ø—É–±–ª—ñ—á–Ω–æ–≥–æ —Å–∞–π—Ç—É:', response.data.length, '—à—Ç')
      console.log('üì¶ –ü–µ—Ä—à—ñ 3 –ø—Ä–æ–¥—É–∫—Ç–∏:', response.data.slice(0, 3).map(p => ({ id: p.id, name: p.name.ua })))

      set({
        products: response.data,
        isLoading: false,
        error: null,
        isInitialized: true
      })

      return { success: true, data: response.data }
    } catch (error) {
      console.error('‚ùå –ü–æ–º–∏–ª–∫–∞ API –¥–ª—è –ø—É–±–ª—ñ—á–Ω–æ–≥–æ —Å–∞–π—Ç—É:', error.message)
      console.log('üîÑ Fallback –¥–æ —Å—Ç–∞—Ç–∏—á–Ω–∏—Ö –¥–∞–Ω–∏—Ö...')

      // Fallback –¥–æ —Å—Ç–∞—Ç–∏—á–Ω–∏—Ö –¥–∞–Ω–∏—Ö –∑ –∫–æ–Ω—Å—Ç–∞–Ω—Ç–∏
      const staticProducts = productsData.samples || []
      console.log('üì¶ –ó–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–æ —Å—Ç–∞—Ç–∏—á–Ω–∏—Ö –ø—Ä–æ–¥—É–∫—Ç—ñ–≤:', staticProducts.length, '—à—Ç')

      set({
        products: staticProducts,
        error: `–í–∏–∫–æ—Ä–∏—Å—Ç–æ–≤—É—é—Ç—å—Å—è —Å—Ç–∞—Ç–∏—á–Ω—ñ –¥–∞–Ω—ñ. API –Ω–µ–¥–æ—Å—Ç—É–ø–Ω–∏–π.`,
        isLoading: false,
        isInitialized: true
      })

      return { success: false, error: error.message, fallbackUsed: true }
    }
  },

  // –û—Ç—Ä–∏–º–∞–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—É –∑–∞ ID
  getProductById: (id) => {
    const { products } = get()
    return products.find(product => product.id === id)
  },

  // –û—Ç—Ä–∏–º–∞–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –∑–∞ –∫–∞—Ç–µ–≥–æ—Ä—ñ—î—é
  getProductsByCategory: (category) => {
    const { products } = get()

    if (category === 'all') {
      return products
    }

    return products.filter(product => {
      switch (category) {
        case 'gabbro':
          return product.textureId?.includes('black')
        case 'granite':
          return product.textureId?.includes('red-brown') || product.textureId?.includes('gray')
        case 'green':
          return product.textureId?.includes('green')
        case 'labradorite':
          return product.textureId?.includes('labradorite')
        default:
          return true
      }
    })
  },

  // –û—Ç—Ä–∏–º–∞–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –≤ –Ω–∞—è–≤–Ω–æ—Å—Ç—ñ
  getInStockProducts: () => {
    const { products } = get()
    return products.filter(product => product.inStock)
  },

  // –û–Ω–æ–≤–ª–µ–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ (–¥–ª—è –≤–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è –∑ –∞–¥–º—ñ–Ω–ø–∞–Ω–µ–ª—ñ)
  refreshProducts: async () => {
    console.log('üîÑ –û–Ω–æ–≤–ª–µ–Ω–Ω—è –ø—Ä–æ–¥—É–∫—Ç—ñ–≤ –¥–ª—è –ø—É–±–ª—ñ—á–Ω–æ–≥–æ —Å–∞–π—Ç—É...')
    return get().loadProducts(true)
  },

  // –û—á–∏—Å—Ç–∫–∞ –ø–æ–º–∏–ª–æ–∫
  clearError: () => {
    set({ error: null })
  },

  // –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
  getProductsStats: () => {
    const { products } = get()

    const stats = {
      total: products.length,
      inStock: products.filter(p => p.inStock).length,
      outOfStock: products.filter(p => !p.inStock).length,
      categories: {
        gabbro: products.filter(p => p.textureId?.includes('black')).length,
        granite: products.filter(p =>
          p.textureId?.includes('red-brown') || p.textureId?.includes('gray')
        ).length,
        green: products.filter(p => p.textureId?.includes('green')).length,
        labradorite: products.filter(p => p.textureId?.includes('labradorite')).length
      }
    }

    return stats
  },

  // –ü–æ—à—É–∫ –ø—Ä–æ–¥—É–∫—Ç—ñ–≤
  searchProducts: (query) => {
    const { products } = get()

    if (!query) return products

    const searchQuery = query.toLowerCase()
    return products.filter(product =>
      product.name.ua.toLowerCase().includes(searchQuery) ||
      product.name.en.toLowerCase().includes(searchQuery) ||
      product.description.ua.toLowerCase().includes(searchQuery) ||
      product.description.en.toLowerCase().includes(searchQuery) ||
      product.features.ua.some(feature => feature.toLowerCase().includes(searchQuery)) ||
      product.features.en.some(feature => feature.toLowerCase().includes(searchQuery))
    )
  }
}))

export default useProductsStore